          </button>
          <button class="control-btn next-btn" @click="playNext" :disabled="!hasSongs">
            <img src="/icons/cute-next.svg" alt="ä¸‹ä¸€é¦–" class="control-icon" />
          </button>
          <button class="control-btn shuffle-btn" @click="toggleShuffle" :class="{ active: isShuffling }">
