        <!-- éŸ³é‡æ§åˆ¶ -->
        <div class="volume-control" v-if="hasSongs">
          <button class="volume-btn" @click="toggleMute">
            <img src="/icons/cute-volume.svg" alt="éŸ³é‡" class="volume-icon" />
          </button>
          <div class="volume-slider-wrapper">
            <input 
              type="range" 
              class="volume-slider" 
              :value="isMuted ? 0 : volume" 
              @input="updateVolume"
              min="0" 
              max="100"
            />
          </div>
          <span class="volume-text">{{ isMuted ? 0 : volume }}%</span>
        </div>
      </div>
    </div>
    
    <!-- éŸ³ä¹åˆ—è¡¨å’Œå†å²è®°å½•é€‰é¡¹å¡ - é‡‡ç”¨å¡ç‰‡å¼è®¾è®¡ -->
    <div class="music-tabs">
      <div class="tabs-header">
        <button 
          class="tab-btn" 
          :class="{ active: activeTab === 'songs' }" 
          @click="activeTab = 'songs'"
        >
          <img src="/icons/cute-music.svg" alt="éŸ³ä¹åˆ—è¡¨" class="tab-icon" />
          éŸ³ä¹åˆ—è¡¨
        </button>
        <button 
          class="tab-btn" 
          :class="{ active: activeTab === 'history' }" 
          @click="activeTab = 'history'"
        >
          <img src="/icons/cute-history.svg" alt="å†å²è®°å½•" class="tab-icon" />
          å†å²è®°å½•
        </button>
      </div>
      
      <!-- éŸ³ä¹åˆ—è¡¨å†…å®¹ -->
      <div class="tab-content" v-show="activeTab === 'songs'">
        <div class="songs-filter">
          <div class="search-input-wrapper">
            <img src="/icons/cute-search.svg" alt="æœç´¢" class="search-icon" />
            <input 
              type="text" 
              class="search-input" 
              v-model="searchQuery" 
              placeholder="æœç´¢æ­Œæ›²ã€æ­Œæ‰‹..."
            />
            <button v-if="searchQuery" class="clear-search" @click="clearSearch">
              âœ•
            </button>
          </div>
          <div class="filter-stats">
            æ‰¾åˆ° {{ filteredSongs.length }} é¦–æ­Œæ›²
          </div>
        </div>
        <div class="songs-list">
          <div 
            v-for="(song, index) in filteredSongs" 
            :key="song.id"
            class="song-item"
            :class="{ 
              'active': currentSongIndex === index,
                'hovered': hoveredSongIndex === index 
              }"
            @click="playSong(index)"
            @mouseenter="hoveredSongIndex = index"
            @mouseleave="hoveredSongIndex = -1"
          >
            <div class="song-number">{{ index + 1 }}</div>
            <div class="song-cover-mini">
              <img :src="song.cover || '/default-album.png'" alt="æ­Œæ›²å°é¢" />
            </div>
            <div class="song-details">
              <div class="song-name">{{ song.title }}</div>
              <div class="song-artist-name">{{ song.artist }}</div>
            </div>
            <div class="song-album">{{ song.album || 'æœªçŸ¥ä¸“è¾‘' }}</div>
            <div class="song-duration">{{ formatTime(song.duration) }}</div>
            <div class="song-actions-hover">
              <button class="action-btn-play" @click.stop="playSong(index)">
                <img src="/icons/cute-play-btn.svg" alt="æ’­æ”¾" class="icon-image" />
              </button>
            </div>
          </div>
          <div v-if="filteredSongs.length === 0" class="empty-state">
        <img src="/icons/cute-music.svg" alt="æš‚æ— æ­Œæ›²" class="empty-icon" />
        <div class="empty-text">{{ searchQuery ? 'æ²¡æœ‰æ‰¾åˆ°åŒ¹é…çš„æ­Œæ›²' : 'æš‚æ— æ­Œæ›²' }}</div>
        <div class="empty-hint">
          {{ searchQuery ? 'è¯•è¯•å…¶ä»–å…³é”®è¯æœç´¢' : 'æ·»åŠ ä¸€äº›æ­Œæ›²å¼€å§‹äº«å—éŸ³ä¹å§' }}
        </div>
      </div>
        </div>
      </div>
      
      <!-- å†å²è®°å½•å†…å®¹ -->
      <div class="tab-content" v-show="activeTab === 'history'">
        <div class="history-header">
          <h3 class="history-title">æ’­æ”¾å†å²</h3>
          <button v-if="playHistory.length > 0" class="clear-history" @click="clearAllHistory">
            æ¸…ç©ºå†å²
          </button>
        </div>
        <div class="history-list">
          <div 
            v-for="(record, index) in playHistory" 
            :key="index"
            class="history-item"
            @click="playHistorySong(record.songIndex)"
            @mouseenter="hoveredHistoryIndex = index"
            @mouseleave="hoveredHistoryIndex = -1"
          >
            <div class="history-time">{{ formatDate(record.timestamp) }}</div>
            <div class="history-cover-mini">
              <img :src="record.song.cover || '/default-album.png'" alt="æ­Œæ›²å°é¢" />
            </div>
            <div class="history-song-info">
              <div class="history-song-name">{{ record.song.title }}</div>
              <div class="history-song-artist">{{ record.song.artist }}</div>
            </div>
            <div class="history-actions">
              <button 
                class="history-remove-btn" 
                @click.stop="removeFromHistory(index)"
                :class="{ visible: hoveredHistoryIndex === index }"
              >
                âœ•
              </button>
            </div>
          </div>
          <div v-if="playHistory.length === 0" class="empty-state">
            <img src="/icons/cute-history.svg" alt="æš‚æ— å†å²" class="empty-icon" />
            <div class="empty-text">æš‚æ— æ’­æ”¾å†å²</div>
            <div class="empty-hint">æ’­æ”¾éŸ³ä¹åä¼šè‡ªåŠ¨è®°å½•åœ¨è¿™é‡Œ</div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- è¿·ä½ æ’­æ”¾å™¨å·²ç§»é™¤ -->
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'

export default {
  name: 'MusicPlayer',
  data() {
    return {
      activeTab: 'songs',
      searchQuery: '',
      isDragging: false,
      hoveredSongIndex: -1,
      hoveredHistoryIndex: -1
    }
  },
  computed: {
    ...mapState([
      'isPlaying',
      'currentTime',
      'duration',
      'volume',
      'isMuted',
      'currentSongIndex',
      'isRepeating',
      'isShuffling',
      'likedSongs',
      'songs',
      'playHistory'
    ]),
    ...mapGetters([
      'currentSong',
      'hasSongs',
      'progress',
      'isCurrentSongLiked'
    ]),
    filteredSongs() {
      if (!this.searchQuery) return this.songs
      const query = this.searchQuery.toLowerCase()
      return this.songs.filter(song => 
        song.title.toLowerCase().includes(query) || 
        song.artist.toLowerCase().includes(query) ||
        song.album.toLowerCase().includes(query)
      )
    },
    // è®¡ç®—å½“å‰æ’­æ”¾èƒŒæ™¯æ ·å¼ - åŸºäºä¸“è¾‘å°é¢çš„æ¸å˜èƒŒæ™¯
    playerBgStyle() {
      if (!this.currentSong?.cover) {
        return {
          background: 'linear-gradient(135deg, rgb(215, 255, 232) 0%, rgb(231, 224, 255) 100%)'
        }
      }
      return {
        background: 'linear-gradient(135deg, rgb(215, 255, 232) 0%, rgb(231, 224, 255) 100%)'
      }
    }
  },
  mounted() {
    // åŠ è½½æ’­æ”¾å†å²
    this.loadPlayHistory()
    
    // æ·»åŠ å…¨å±€é”®ç›˜å¿«æ·é”®æ”¯æŒ
    document.addEventListener('keydown', this.handleKeydown)
  },
  beforeDestroy() {
    // ç§»é™¤äº‹ä»¶ç›‘å¬
    document.removeEventListener('keydown', this.handleKeydown)
  },
  methods: {
    ...mapActions([
      'togglePlay',
      'playSong',
      'playPrevious',
      'playNext',
      'toggleRepeat',
      'toggleShuffle',
      'toggleLike',
      'updateProgress',
      'updateVolume',
      'toggleMute',
      'handleAudioEnd',
      'clearAllHistory',
      'removeFromHistory',
      'loadPlayHistory'
    ]),
    
    // è·å–éŸ³é‡å›¾æ ‡
    getVolumeIcon() {
      if (this.isMuted) return 'ğŸ”‡'
      if (this.volume < 30) return 'ğŸ”ˆ'
      if (this.volume < 70) return 'ğŸ”‰'
      return 'ğŸ”Š'
    },
    
    // åˆ†äº«æ­Œæ›²
    shareSong() {
      if (!this.currentSong) return
      
      const shareText = `${this.currentSong.title} - ${this.currentSong.artist}`
      
      // å¤åˆ¶åˆ°å‰ªè´´æ¿
      if (navigator.clipboard && window.isSecureContext) {
        navigator.clipboard.writeText(shareText).then(() => {
          alert('åˆ†äº«ä¿¡æ¯å·²å¤åˆ¶åˆ°å‰ªè´´æ¿')
        }).catch(err => {
          console.error('å¤åˆ¶å¤±è´¥:', err)
        })
      } else {
        alert(`åˆ†äº«: ${shareText}`)
      }
    },
    
    // æ¸…ç©ºæœç´¢
    clearSearch() {
      this.searchQuery = ''
    },
    
    // é”®ç›˜å¿«æ·é”®å¤„ç†
    handleKeydown(event) {
      // å¦‚æœç„¦ç‚¹åœ¨è¾“å…¥æ¡†ä¸­ï¼Œåˆ™ä¸å¤„ç†å¿«æ·é”®
      if (event.target.tagName === 'INPUT') return
      
      switch (event.key.toLowerCase()) {
        case ' ':
          event.preventDefault()
          this.togglePlay(this.$refs.audioPlayer)
          break
        case 'arrowright':
          event.preventDefault()
          const newTime = Math.min(this.currentTime + 10, this.duration)
          this.$store.commit('SET_CURRENT_TIME', newTime)
          if (this.$refs.audioPlayer) {
            this.$refs.audioPlayer.currentTime = newTime
          }
          break
        case 'arrowleft':
          event.preventDefault()
          const prevTime = Math.max(this.currentTime - 10, 0)
          this.$store.commit('SET_CURRENT_TIME', prevTime)
          if (this.$refs.audioPlayer) {
            this.$refs.audioPlayer.currentTime = prevTime
          }
          break
        case 'arrowup':
          event.preventDefault()
          const newVolUp = Math.min(this.volume + 5, 100)
          this.updateVolume(newVolUp)
          break
        case 'arrowdown':
          event.preventDefault()
          const newVolDown = Math.max(this.volume - 5, 0)
          this.updateVolume(newVolDown)
          break
      }
    },
    
    // æ ¼å¼åŒ–æ—¶é—´ï¼ˆç§’ -> mm:ssï¼‰
    formatTime(seconds) {
      if (!seconds || isNaN(seconds)) return '00:00'
      
      const mins = Math.floor(seconds / 60)
      const secs = Math.floor(seconds % 60)
      return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
    },
    
    // æ ¼å¼åŒ–æ—¥æœŸ
    formatDate(timestamp) {
      const date = new Date(timestamp)
      return date.toLocaleString('zh-CN', {
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit'
      })
    },
    
    // æ’­æ”¾å†å²è®°å½•ä¸­çš„æ­Œæ›²
    playHistorySong(songIndex) {
      this.playSong(songIndex)
      // æ»šåŠ¨åˆ°å½“å‰æ’­æ”¾æ­Œæ›²
      this.$nextTick(() => {
        const songElement = document.querySelector(`.song-item.active`)
        if (songElement) {
          songElement.scrollIntoView({ behavior: 'smooth', block: 'center' })
        }
      })
    },
    
    // åˆ‡æ¢æ’­æ”¾çŠ¶æ€ï¼Œç›´æ¥é€šè¿‡Vuex storeæ§åˆ¶
    togglePlay() {
      this.$store.dispatch('togglePlay')
    },
    
    // æ›´æ–°è¿›åº¦æ¡ï¼Œç›´æ¥é€šè¿‡Vuex storeæ§åˆ¶
    updateProgress(event) {
      const newProgress = parseFloat(event.target.value)
      const newTime = (newProgress / 100) * this.duration
      this.$store.commit('SET_CURRENT_TIME', newTime)
    },
    
    // æ›´æ–°éŸ³é‡ï¼Œç›´æ¥é€šè¿‡Vuex storeæ§åˆ¶
    updateVolume(event) {
      const newVolume = typeof event === 'number' ? event : parseFloat(event.target.value)
      this.$store.dispatch('updateVolume', newVolume)
    },
    
    // åˆ‡æ¢é™éŸ³çŠ¶æ€ï¼Œç›´æ¥é€šè¿‡Vuex storeæ§åˆ¶
    toggleMute() {
      this.$store.dispatch('toggleMute')
    }
  }
}
</script>

<style scoped>
