/* ä¸“è¾‘å°é¢åŒºåŸŸ - å¢å¼ºè§†è§‰æ•ˆæœ */
.album-cover-container {
  position: relative;
  margin-bottom: 30px;
}

.album-shadow {
  position: absolute;
